
.on {
    color: #42b983;
}
.onlyOne {
    padding-left: .24rem;
}
.more {
    margin-top: .5rem;
}
button {
    border-radius: 4px;
    display: block;
    /*border: 1px solid;*/
    padding: .05rem;
    margin-top: .1rem;
}

  /* 弹出层样式 */
.cover {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, .5);
}
.coverWrap {
  background: #fff;
  width: 1.5rem;
  height: 1rem;
  border-radius: 5px;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -.75rem;
  margin-top: -.5rem;
}
.coverWrap button {
  border: 0;
  display: inline-block;
  background: #fff;
  color: #15a5ec;
  position: absolute;
  right: .1rem;
  bottom: .05rem;
}
.coverWrap > div {
  padding-top: .2rem;
}



